<template>
    <div class="selectPosSearch">
        <div class="search-pos-wrap">
            <el-input class="search-pos-input" v-model="searchVal" size="large"
                :clearable="true" suffix="搜索"
                placeholder="请输入搜索内容" :autofocus="true" @keydown="handleKeydown">
                <template #prepend>
                    <el-button class="search-pos-back" :icon="ArrowLeftBold" @click="handleGoback"/>
                </template>
                <template #append>
                    <span class="search-pos-search" @click="handleSearch">搜索</span>
                </template>
            </el-input>
        </div>
    </div>
</template>
<script>
import { ArrowLeftBold, Search } from '@element-plus/icons-vue'
export default {
    name: 'selectPosSearch',
    components: {
    
    },
    props: {
    },
    computed: {
        
        
    },
    data() {
        return {
            searchVal: '',
            ArrowLeftBold,
            Search,
        }
    },
    methods: {
        handleKeydown(val) {
            if(val.key==='Enter') {
                this.$emit('search', this.searchVal);
            }
        },
        handleGoback() {
            this.$emit('close');
        },
        handleSearch() {
            this.$emit('search', this.searchVal);
        }
    }
}

</script>

