<template>
  <div class="select-pos">
    <div id="select-pos-mapContainer"></div>
    <topBar title="地点位置" class="select-pos-topbar"></topBar>
    <div class="select-pos-content">
      <searchButton text="距离选点较远，可以搜索地点找位置" @click="goToSearchPosPage"></searchButton>
      <div class="select-pos-list">
        <posCardLocation class="select-pos-item" v-for="(item, ind) in positionList" :key="ind" :positionObj="item" 
          :activeIndex="activePositionIndex" @click="handleSelectPosition(item)"></posCardLocation>
      </div>
    </div>
    <customButton v-if="activePositionIndex" class="bottomBtn" text="确定" showShadow @btnClick="handleSure"></customButton>
    <selectPosSearch class="select-pos-search" v-if="showSelectPosSearch" @search="handleSearchPosition" @close="showSelectPosSearch = false"></selectPosSearch>
  </div>
</template>

<script>
import topBar from "@/components/topBar.vue";
import searchButton from "@/components/searchButton.vue";
import selectPosSearch from "@/components/selectPosSearch.vue";
import posCardLocation from "@/components/position/posCardLocation.vue";
import customButton from "@/components/customButton.vue"
import AMapLoader from "@amap/amap-jsapi-loader"; // 按需引入依赖
import { getSearchPositionList } from "@/api/feedback";

export default {
  name: 'selectPos',
  components: {
    topBar,
    searchButton,
    selectPosSearch,
    posCardLocation,
    customButton,
  },
  data() {
    return {
      map: null,
      mapMarker: null,
      AMap: null,
      // defaultCenter: [114.268691, 30.401227],   // 默认加载的中心点坐标，待修改
      showSelectPosSearch: false,
      positionList: [],
      activePositionIndex: '',
      showShadow: true,
    }
  },
  mounted() {
    this.initMap();
  },
  created() {

  },
  methods: {
    initMap() {
        AMapLoader.load({
            key: "3636a38201aa7ca7aa80ee4209995c7e", // 申请好的Web端开发者Key，首次调用 load 时必填
            version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
            // version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
            plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
        })
        .then((AMap) => {
            console.log(AMap);
            // 初始化地图
            this.map = new AMap.Map("select-pos-mapContainer", {
                viewMode: "2D", // 是否为3D地图模式
                zoom: 9, // 初始化地图级别
                center: null, //中心点坐标
                resizeEnable: true,
                mapStyle: "amap://styles/whitesmoke", //设置地图的显示样式
            });
            this.AMap = AMap;
            
            
        })
        .catch((e) => {
            console.log('initMap catch error------', e);
        });
    },
    goToSearchPosPage(){
      console.log('goToSearchPosPage-------');
      this.showSelectPosSearch = true;
    },
    handleSearchPosition(aa) {
      console.log('handleSearchPosition---aa--', aa);
      getSearchPositionList().then((res) => {
        this.positionList = res.data;
        this.activePositionIndex = '';
      }).finally(() => {
        this.showSelectPosSearch = false;
      })
    },
    handleSelectPosition(item) {
      this.activePositionIndex = item.id;
      let center = [item.longitude, item.latitude];
      if(this.mapMarker) {
        this.map.remove([this.mapMarker]);
        this.mapMarker = null;
      }
      this.map.setCenter(center);
      this.mapMarker = new this.AMap.Marker({
          position: center,
          icon: '//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png',
          anchor:'bottom-center',
          offset: new this.AMap.Pixel(0, 0)
      });
      this.mapMarker.setMap(this.map);
      this.mapMarker.setLabel({
          direction:'top',
          offset: new this.AMap.Pixel(0, 0),  //设置文本标注偏移量
          content: `<div class='info'>${item.location}</div>`, //设置文本标注内容
      });
      this.map.setFitView();
    },
    handleSure() {

    }
  }
}
</script>

