<template>
    <div class="topbar">
       <div class="topbar-left" @click="goBack">
        <img :src="gobackImg" alt="">
        <span>{{title}}</span>
       </div>
       <div class="topbar-right" @click="goShare">
        <img :src="goshareImg" alt="">
       </div>
    </div>
</template>
<script>

export default {
    name: 'topBar',
    components: {
    
    },
    props: {
        iconColor: {
            type: String,
            default: 'dark'
        },
        title: {
            type: String,
            default: ''
        },
        customGoBack: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        gobackImg() {
            return this.iconColor === 'dark' ? require('../assets/goback-dark.svg') : require('../assets/goback-light.svg');
        },
        goshareImg() {
            return this.iconColor === 'dark' ? require('../assets/goshare-dark.svg') : require('../assets/goshare-light.svg');
        },
        
    },
    data() {
        return {
            
        }
    },
    methods: {
        goBack() {
            console.log('goBack......')
            if(this.customGoBack) {
                this.$emit('goBack');
            } else {
                this.$router.back();
            }
        },
        goShare() {
            console.log('goShare......')
        },
    }
}

</script>

