<template>
  <div class="homePage">
    <slides :slideData="slidesArr"></slides>
    <category :categoryData="categoryArr" @gotoCategory="handleGotoCategory"></category>
    <div class="position-list">
      <div class="position-category">
        <div class="position-category-wrap">
          <div class="position-category-list" id="position-category-list">
            <span v-for="(item, index) in categoryAll" :key="index" :class="{'active': item.icon === activeCategoryIcon}" @click="changeActiveCategory(item, index)">{{ item.name }}</span>
          </div>
        </div>
      </div>
      <posCardPic v-for="(item, index) in recommendPlaceArr" :key="index" :positionObj="item"></posCardPic>
    </div>    
    <customButton class="bottomBtn" text="进入地图" showShadow isBig @btnClick="goToMap"></customButton>
  </div>
</template>

<script>
import { getSlides, getRecommends } from "@/api/home";
import slides from "@/components/slides.vue"
import category from "@/components/category.vue"
import customButton from "@/components/customButton.vue"
import posCardPic from "@/components/position/posCardPic.vue"

export default {
  name: 'App',
  components: {
    slides,
    category,
    customButton,
    posCardPic,
  },
  data() {
    return {
      slidesArr: [],
      categoryArr: require('@/constData/categoryList.json'),
      activeCategoryIcon: 'all',
      activeCategoryIndex: 0,
      recommendPlaceArr: []
    }
  },
  computed: {
    categoryAll() {
      let arr = [{
        icon: 'all',
        name: '热门推荐'
      }, ...this.categoryArr]
      return arr;
    }
  },
  methods: {
    getSlides() {
      getSlides().then((res) => {
        console.log('getSlides----', res);
        this.slidesArr = res.data;
      });
    },
    getRecommends() {
      // this.activeCategoryIcon
      getRecommends().then((res) => {
        console.log(`getRecommends----icon: ${this.activeCategoryIcon}, res:`, res);
        this.recommendPlaceArr = res.data;
      });
    },
    handleGotoCategory(item) {

      console.log('handleGotoCategory--------', item)
      this.goToMap(item.id);
    },
    goToMap(categoryId) {
      console.log('goToMap--------', categoryId)
      // if(categoryId) {
      //   this.$router.push({ path: `/map?category=${categoryId}` })
      // } else {
      //   this.$router.push({ path: `/map` })
      // }
      this.$router.push({ 
        name: 'map',
        query: {
          category: categoryId
        }
      })
    },
    changeActiveCategory(item, index) {
      console.log('changeActiveCategory------', item)
      const dataBar = document.querySelector('.position-category-wrap');
      let list = document.getElementById("position-category-list"); // 通过id选择器获取到列表元素
      let marginRight = 25;
      if(index > this.activeCategoryIndex) {
        //点击右边的元素，向左滑动, scrollLeft增加, 增加值为下一个元素(右边一个)的宽度
        let nextWidth = list.children[index + 1] ? list.children[index + 1].offsetWidth : 0;
        dataBar.scrollLeft = dataBar.scrollLeft + (nextWidth + marginRight);
      } else if(index < this.activeCategoryIndex) {
        // 点击左边的元素，向右滑动，scrollLeft减少，减少值为前一个元素(左边一个)的宽度
        let beforeWidth = list.children[index - 1] ? list.children[index - 1].offsetWidth : 0;
        dataBar.scrollLeft = dataBar.scrollLeft - (beforeWidth + marginRight);
      }
      this.activeCategoryIndex = index;
      this.activeCategoryIcon = item.icon;
      this.getRecommends();
    }
  },
  created() {
    this.getSlides();
    this.getRecommends();
  },
}
</script>

<style lang="less" scoped>
@import url('@/less/common.less');
@category-title-color-common: rgba(51, 51, 51, 0.5);
@category-title-fontSize-common: 14px;
@category-title-color-active: rgba(51, 51, 51, 1);
@category-title-fontSize-active: 18px;
.homePage {
  width: 100vw;
  height: 100vh;
  .position-list {
    padding: 0px 16px 60px 16px;
    .position-category{
      position: sticky;
      top: 0;
      z-index: 1;
      .position-category-wrap{
        position: relative;
        background: #ffffff;
        height: 42px;
        overflow-x: scroll;
        &::-webkit-scrollbar{
            display: none;
        }
        .position-category-list{
          position: absolute;
          left: 0;
          // .horizontalSlide();
          white-space: nowrap;
          padding: 8px 0;
          background: #ffffff;
          span{
            margin-right: 25px;
            font-weight: 500;
            font-size: @category-title-fontSize-common;
            color: @category-title-color-common;
            &.active{
              font-size: @category-title-fontSize-active;
              color: @category-title-color-active;
            }
            &:last-child {
              margin-right: 0px;
            }
          }
        }
      }
    }
  }
  .bottomBtn {
    position: fixed;
    bottom: 16px;
  }
}
</style>
