import Mock from "mockjs";

import slides from "./api/home/slides.json";
import recommends from "./api/home/recommends.json";
import streets from "./api/map/streets.json";
import placeList from "./api/map/placeList.json";
import placeDetail from "./api/map/placeDetail.json";
import search from "./api/map/search.json";
import searchPos from "./api/feedback/searchPos.json";

Mock.setup({
  timeout: "100-1000",
});

// 首页
Mock.mock("/api/home/slide", slides);
Mock.mock("/api/home/recommends", recommends);
// 地图页
Mock.mock("/api/map/streets", streets);
Mock.mock("/api/map/placeList", placeList);
Mock.mock("/api/map/placeDetail", placeDetail);
Mock.mock("/api/map/search", search);
// 反馈
Mock.mock("/api/feedback/searchPos", searchPos);

