<template>
  <div class="mapPage">
    <topBar></topBar>
    <searchButton @click="goToSearchPage"></searchButton>
    <div class="changebar-list">
      <formSelect class="changebar-select changebar-street" holderText="全部街道" 
        :required="false"  :selectData="streetArr" @change="handleStreetChange"></formSelect>
      <formSelect class="changebar-select changebar-category" holderText="全部类别" 
        :required="false" :selectData="categoryArr" @change="handleCategoryChange" :defaultSelectVal="categoryDefaultVal"></formSelect>
    </div>
    <div class="map-container">
      <mapContainer v-show="true" :placeList="placeList" @openDetail="handleOpenDetail" @closeDetail="handleCloseDetail"></mapContainer>
    </div>
    <mapPositionDetail class="map-detail-wrap" :class="{'active': showPosDetail}"  :positionData="posDetailData"></mapPositionDetail>
    <div class="map-feedback" @click="handleFeedback">反馈</div>
  </div>
</template>

<script>
import { getPlaceList } from "@/api/map";
import topBar from "@/components/topBar.vue"
import searchButton from "@/components/searchButton.vue";
import formSelect from "@/components/form/formSelect.vue"
import mapContainer from "@/components/map/mapContainer.vue"
import mapPositionDetail from "@/components/map/mapPositionDetail.vue"

export default {
  name: 'App',
  components: {
    topBar,
    searchButton,
    formSelect,
    mapContainer,
    mapPositionDetail,
  },
  data() {
    return {
      showPosDetail: false,
      posDetailData: {},
      placeList: [],
      streetVal: '',
      categoryVal: '',
      categoryDefaultVal: '',
      streetArr: require('@/constData/streetList.json'),
      categoryArr: require('@/constData/categoryList.json'),
    }
  },
  mounted() {

  },
  created() {
    let category = this.$route.query.category;
    this.categoryDefaultVal = category;
    console.log('map his.$route.query.category---',category)
    this.getPlaceList();
  },
  methods: {
    getPlaceList() {
      getPlaceList().then((res) => {
        console.log(`getPlaceList----res:`, res);
        this.placeList = res.data;
      });
    },
    handleOpenDetail(item) {
      console.log('---====--', item);
      this.posDetailData = item;
      this.showPosDetail = true;
    },
    handleCloseDetail() {
      this.showPosDetail = false;
    },
    handleFeedback() {
      this.$router.push({ path: '/feedback' })
    },
    goToSearchPage() {
      this.$router.push({ path: '/search' })
    },
    handleStreetChange(item) {
      console.log('handleStreetChange----', item);
      this.streetVal = item;
    },
    handleCategoryChange(item) {
      console.log('handleCategoryChange----', item);
      this.categoryVal = item;
    },
  }
}
</script>

