@import url('@/less/common.less');
.mapPage{
  height: 100vh;
  display: flex;
  flex-direction: column;
  .changebar-list {
    padding: 4px 12px;
    display: flex;
    align-items: center;
    .changebar-select{
      width: 120px;
      &.changebar-street {
        margin-right: 16px;
      }
    }
  }
  .map-container{
    // background: yellowgreen;
    flex: 1;
  }
  .map-detail-wrap {
    box-sizing: border-box;
    transition: height  0.5s;
    height: 0px;
    width: 100vw;
    &.active {
      height: 186px;
    }
  }
  .map-feedback {
    position: fixed;
    bottom: 40px;
    right: 8px;
    height: 42px;
    width: 42px;
    font-size: 14px;
    border-radius: 10px;
    background: #ffffff;
    .alignCenterTwoDirection();
}
}

</style>
