<template>
  <div id="map-container">
  </div>
</template>

<script>
import AMapLoader from "@amap/amap-jsapi-loader"; // 按需引入依赖

export default {
  name: 'mapContainer',
  components: {
  
  },
  props: {
    placeList: {
        type: Array,
        default: () => {
          return []
        }
    }
  },
  computed: {
      
  },
  watch: {
    placeList:{
        deep: true,
        handler(val) {
            if(this.map) {
                console.log('mapContainer placeList val------ ', val)
                this.setMapCenterAndRange();
                this.setMapMarker();
            }
        }
    }
  },
  data() {
      return {
          map: null,
          AMap: null,
          infoWindow: null,
          defaultCenter: [114.268691, 30.401227],   // 默认加载的中心点坐标，待修改
          tempPic: require('@/assets/Shape.png'),
      }
  },
  methods: {
        initMap() {
            AMapLoader.load({
                key: "3636a38201aa7ca7aa80ee4209995c7e", // 申请好的Web端开发者Key，首次调用 load 时必填
                version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                // version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
            })
            .then((AMap) => {
                console.log(AMap);
                // 初始化地图
                this.map = new AMap.Map("map-container", {
                    viewMode: "2D", // 是否为3D地图模式
                    // zoom: 15, // 初始化地图级别
                    // center: this.defaultCenter, //中心点坐标
                    center: null, //中心点坐标
                    resizeEnable: true,
                    mapStyle: "amap://styles/whitesmoke", //设置地图的显示样式
                });
                this.AMap = AMap;
                this.map.on('click', this.handleMapClick);
                this.setMapCenterAndRange();
                this.setMapMarker();
                
            })
            .catch((e) => {
                console.log('initMap catch error------', e);
            });
        },
        setMapCenterAndRange() {
            let longArr = this.placeList.map(item => item.longitude);
            let latArr = this.placeList.map(item => item.latitude);
            let longCenter = (Math.max(...longArr) + Math.min(...longArr))/2;
            let latCenter = (Math.max(...latArr) + Math.min(...latArr))/2;
            // console.log('longArr----', longArr);
            // console.log('longCenter----', longCenter);
            // console.log('latCenter----', latCenter);
            this.map.setCenter([longCenter, latCenter]);
        },
        setMapMarker() {
            for(let item of this.placeList) {
                let center = [item.longitude, item.latitude];
                let marker = new this.AMap.Marker({
                    position: center,
                    icon: '//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png',
                    anchor:'bottom-center',
                    offset: new this.AMap.Pixel(0, 0)
                });
                marker.setMap(this.map);
                marker.setLabel({
                    direction:'right',
                    offset: new this.AMap.Pixel(0, 0),  //设置文本标注偏移量
                    content: `<div class='info'>${item.name}</div>`, //设置文本标注内容
                });
                marker.on('click', (e) => {
                    console.log('mapMarker click e: ', e);
                    console.log('mapMarker click item: ', item);
                    this.map.setCenter([item.longitude, item.latitude]);
                    this.openInfoWindow(item);
                })
            }
            this.map.setFitView();
        },
        openInfoWindow(item) {
            item.pic = this.tempPic;
            let AMap = this.AMap;
            let domStr = `
                <div class='mapMarker' style='display: flex;justify-content: center;align-items: center;'>
                    <img src='${item.pic}' alt='' style='margin-right: 16px;'>
                    <div class='map-marker-left'>
                        <div class='map-marker-title' style='font-size: 14px;'>${item.name}</div>
                        <div class='map-marker-description' style='font-size: 12px;'>${item.description}</div>
                    </div>
                </div>
            `;
            this.infoWindow = new AMap.InfoWindow({
                content: domStr  //使用默认信息窗体框样式，显示信息内容
            });  
            this.infoWindow.open(this.map, [item.longitude, item.latitude]);
            this.$emit('openDetail', item);
        },
        closeInfoWindow() {
            if(this.infoWindow) {
                this.infoWindow.close();
                this.$emit('closeDetail');
            }
            console.log('closeInfoWindow this.infoWindow----', this.infoWindow);
        },
        handleMapClick() {
            this.closeInfoWindow();
        }

  },
  mounted() {
      this.initMap();
  },
  unmounted() {
    this.map?.destroy();
  },
}

</script>

<style lang="less" scoped>
@import url('@/less/common.less');
@map-dialog-bg: rgba(0,99,65,1);

#map-container :deep {
  height: 100%;
  width: 100%;
  // background: yellow;
    .amap-icon img {
        width: 16px;
        height: 24px;
    }

    .amap-marker-label{
        border: 0;
        background-color: transparent;
    }

    .amap-logo {
        display: none !important;
    }
    .amap-overlays{
        z-index: 1;
    }
    .amap-info-content {
        background: @map-dialog-bg;
        border-radius: 8px;
        padding: 20px 24px;
        .mapMarker{
            img{
                background: #ffffff;
                width: 28px;
            }
            .map-marker-left{
                .map-marker-title{
                    max-width: 50vw;
                    font-size: 16px;
                    color: rgba(255,255,255,1);
                    .singleLineOverflowHidden();
                }
                .map-marker-description{
                    max-width: 50vw;
                    font-size: 12px;
                    color: rgba(255,255,255,0.5);
                    .singleLineOverflowHidden();
                }
            }
        }
        .amap-info-close{
            display: none;
        }
    }
    .amap-info-sharp{
        border-top-color: @map-dialog-bg;
    }

    .info{
        position: relative;
        margin:0;
        top: 0;
        right: 0;
        min-width: 0;
        background: #ffffff;
        padding: 4px 6px 4px 6px;
        border-radius: 6px;
    }
}
</style>
