let slideList = require('@/mock/api/home/slides.json')
let recommendList = require('@/mock/api/home/recommends.json')

export function getSlides() {
  return new Promise((resolve) => {
    resolve(slideList)
  })
}

export function getRecommends() {
  return new Promise((resolve) => {
    resolve(recommendList)
  })
}