<template>
  <div class="feedbackPage">
    <topBar></topBar>
    <div class="operation-list">
      <div class="add-location" @click="handleAddLocation">
        <div class="title">我要增报</div>
        <img :src="feedbackAddImg" alt="">
      </div>
      <div class="empty-location"></div>
      <div class="error-location" @click="handleErrorLocation">
        <div class="title">我要报错</div>
        <img :src="feedbackErrorImg" alt="">
      </div>
    </div>
    <div class="position-list">
      <div class="position-category">
        <span v-for="(item, index) in categoryAll" :key="index" :class="{'active': item.icon === activeCategoryIcon}" @click="changeActiveCategory(item)">{{ item.name }}</span>
      </div>
      <posCardPic v-for="(item, index) in recommendPlaceArr" :key="index" :positionObj="item"></posCardPic>
    </div> 
  </div>
</template>

<script>
import topBar from "@/components/topBar.vue"
import posCardPic from "@/components/position/posCardPic.vue"
import { getRecommends } from "@/api/home";

export default {
  name: 'App',
  components: {
    topBar,
    posCardPic,
  },
  data() {
    return {
      categoryAll: [{
        icon: 'all',
        name: '热门推荐'
      }],
      activeCategoryIcon: 'all',
      recommendPlaceArr: [],
      feedbackAddImg: require('@/assets/feedback-add-bg.svg'),
      feedbackErrorImg: require('@/assets/feedback-error-bg.svg'),
    }
  },
  methods: {
    handleAddLocation() {
      console.log('handleAddLocation--------')
      this.$router.push({ path: '/addLocation' })
    },
    handleErrorLocation() {
      console.log('handleErrorLocation--------')
      this.$router.push({ path: '/errorLocation' })
    },
    getRecommends() {
      // this.activeCategoryIcon
      getRecommends().then((res) => {
        console.log(`getRecommends----icon: ${this.activeCategoryIcon}, res:`, res);
        this.recommendPlaceArr = res.data;
      });
    },
  },
  created() {
    this.getRecommends();
  },
}
</script>

