<template>
    <div class="search-page">
        <div class="search-top">
            <topBar></topBar>
            <div class="search-input-wrap">
                <el-input class="search-input" v-model="searchVal" size="large" 
                    placeholder="Please Input" :autofocus="true" @keydown="handleSearch"/>
            </div>
        </div>
        <div class="search-result-list">
            <div class="search-result" :class="{'show-bottom': searchList.length}" v-loading="loadingSearch">
                <posCardSearch v-for="(item, index) in searchList" :key="index" :posObj="item"></posCardSearch>
            </div>
            <div class="recommend-title">猜你想去</div>
            <div class="recommend-result" v-loading="loadingRecommend">
                <posCardSearch v-for="(item, index) in recommendList" :key="index" :posObj="item"></posCardSearch>
            </div>
        </div>
    </div>
</template>
<script>
import topBar from "@/components/topBar.vue"
import posCardSearch from "@/components/position/posCardSearch.vue"
import { Search } from '@element-plus/icons-vue'
import { getRecommends } from '@/api/home'
import { getSearchList } from '@/api/map'

export default {
    name: 'searchPage',
    components: {
        topBar,
        posCardSearch,
    },
    props: {
        iconColor: {
            type: String,
            default: 'dark'
        },
        title: {
            type: String,
            default: ''
        }
    },
    computed: {
        
    },
    data() {
        return {
            searchVal: '',
            Search,
            searchList: [],
            recommendList: [],
            loadingSearch: false,
            loadingRecommend: false,
            searchImg: require('@/assets/search.svg'),
        }
    },
    methods: {
        handleSearch(aa) {
            if(aa.key==='Enter') {
                this.getSearchList();
            }
        },
        getSearchList() {
            this.loadingSearch = true;
            getSearchList().then((res) => {
                console.log(`getSearchList getSearchList---- res:`, res);
                this.searchList = res.data;
                this.loadingSearch = false;
            });
        },
        getRecommendsList() {
            this.loadingRecommend = true;
            getRecommends().then((res) => {
                console.log(`getRecommends getRecommends---- res:`, res);
                this.recommendList = res.data;
                this.loadingRecommend = false;
            });
        },
    },
    mounted() {
        this.getSearchList();
        this.getRecommendsList();
    },
}

</script>

