// ==========================

let streets = require('@/mock/api/map/streets.json')
let placeList = require('@/mock/api/map/placeList.json')
let placeDetail = require('@/mock/api/map/placeDetail.json')
let search = require('@/mock/api/map/search.json')

export function getStreetList() {
  return new Promise((resolve) => {
    resolve(streets)
  })
}

export function getPlaceList() {
  return new Promise((resolve) => {
    resolve(placeList)
  })
}

export function getPlaceDetail() {
  return new Promise((resolve) => {
    resolve(placeDetail)
  })
}


export function getSearchList() {
  return new Promise((resolve) => {
    resolve(search)
  })
}