<template>
    <div class="formPos">
        <div class="form-up">
            <div class="form-left">
                <span class="form-input-required" v-if="required">*</span>
                <span class="form-input-title">{{title}}:</span>
            </div>
            <span class="form-input-sub-title">{{subTitle}}</span>
        </div>
        <div class="form-down" id="form-map-container">
            <div class="form-edit-pos" @click="editPos">点击编辑所在位置</div>
        </div>
    </div>
</template>
<script>
import AMapLoader from "@amap/amap-jsapi-loader"; // 按需引入依赖
export default {
    name: 'formPos',
    components: {
    
    },
    props: {
        required: {
            type: Boolean,
            default: true
        },
        title: {
            type: String,
            default: ''
        },
        subTitle: {
            type: String,
            default: ''
        },
    },
    computed: {
      
    },
    data() {
        return {
            map: null,
            AMap: null,
            defaultCenter: [114.268691, 30.401227],  
        }
    },
    methods: {
        initMap() {
            AMapLoader.load({
                key: "3636a38201aa7ca7aa80ee4209995c7e", // 申请好的Web端开发者Key，首次调用 load 时必填
                version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                // version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins: [], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
            })
            .then((AMap) => {
                console.log(AMap);
                // 初始化地图
                this.map = new AMap.Map("form-map-container", {
                    viewMode: "2D", // 是否为3D地图模式
                    // zoom: 15, // 初始化地图级别
                    // center: this.defaultCenter, //中心点坐标
                    center: null, //中心点坐标
                    resizeEnable: true,
                    mapStyle: "amap://styles/whitesmoke", //设置地图的显示样式
                });
                this.AMap = AMap;
                this.map.on('click', this.handleMapClick);
                
            })
            .catch((e) => {
                console.log('initMap catch error------', e);
            });
        },
        editPos() {
            this.$router.push({ path: '/selectPos' })
        },
    },
    mounted() {
      this.initMap();
    },
}

</script>

