import { createRouter, createWebHashHistory } from "vue-router";

// 导入一级路由
import index from '@/views/index'
import home from '@/views/home'
import map from '@/views/map'
import search from '@/views/search'
import posDetail from '@/views/posDetail'
import feedback from '@/views/feedback'
import addLocation from '@/views/addLocation'
import selectPos from '@/views/selectPos'
import errorLocation from '@/views/errorLocation'

const routes = [{
        path: '/',
        redirect: '/index'
    },
    {
        path: '/index',
        name: 'index',
        component: index,
    },
    {
        path: '/home',
        name: 'home',
        component: home,
    },
    {
        path: '/map',
        name: 'map',
        component: map,
        
    },
    {
        path: '/search',
        name: 'search',
        component: search
    },
    {
        path: '/posDetail',
        name: 'posDetail',
        component: posDetail
    },
    {
        path: '/feedback',
        name: 'feedback',
        component: feedback
    },
    {
        path: '/addLocation',
        name: 'addLocation',
        component: addLocation
    },
    {
        path: '/selectPos',
        name: 'selectPos',
        component: selectPos
    },
    {
        path: '/errorLocation',
        name: 'errorLocation',
        component: errorLocation
    },
]




const router = createRouter({
    history: createWebHashHistory(), // 路由模式：history模式
    routes: routes
})

export default router;