<template>
    <div class="mapPositionDetail">
        <div class="mapPositionDetail-container">
            <!-- 上：标题 -->
           <div class="title">{{positionData.name}}</div>
           <!-- 中：内容 -->
           <div class="content">
            <div class="content-left">
                <div class="content-location">
                    <img :src="locationImg" alt="">
                    <span>{{positionData.location}}</span>
                </div>
                <div class="content-distance">距离你{{positionData.distance}}公里</div>
            </div>
            <div class="content-right" @click="goToDetail">
                <span>详情</span>
                <img :src="detailImg" alt="">
            </div>
           </div>
           <!-- 下：导航 -->
           <customButton class="bottomBtn" text="导航"  @btnClick="goToLocation"></customButton>
        </div>
    </div>
</template>
<script>
import customButton from "@/components/customButton.vue"
import AMapLoader from "@amap/amap-jsapi-loader"; // 按需引入依赖

export default {
    name: 'mapPositionDetail',
    components: {
        customButton,
    },
    props: {
        positionData: {
            type: Object,
            default: () => {
                return {}
            }
        },
        
    },
    computed: {
       
    },
    data() {
        return {
            detailImg: require('@/assets/go-detail.svg'),
            locationImg: require('@/assets/location-empty.svg'),
            positionObj: {
                title: '星巴克',
                location: '广州市黄埔区黄埔东路68号（邮编：510700）',
                distance: '3',
            }
        }
    },
    methods: {
        goToDetail() {
            this.$router.push({ path: '/posDetail' })
        },
        goToLocation() {
            console.log('goToLocation-123456-----');
            const item = {
                "location": "黄埔区长洲岛金洲北路563号（酷迪咖啡）",
                "latitude": 23.214468,
                "longitude": 113.476864,
            }
            const {longitude, latitude, address} = item;
            AMapLoader.load({
                key: "3636a38201aa7ca7aa80ee4209995c7e", // 申请好的Web端开发者Key，首次调用 load 时必填
                version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                // version: "1.4.15", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins: ['AMap.Geolocation'], // 需要使用的的插件列表，如比例尺'AMap.Scale'等
            })
            .then((AMap) => {
                // AMap.plugin("AMap.Geolocation", ()=> {
                    let geolocation = new AMap.Geolocation();
                    geolocation.getCurrentPosition((status,result) => {
                        // console.log('Geolocation status---', status)
                        // console.log('Geolocation result---', result)
                        // alert(`result: ${JSON.stringify(result)}`);
                        if(status=="complete"){
                            // alert("获取到了定位！");
                            let app_url = `${this.getScheme()}?sourceApplication=com.mzwu.www&slat=${result.position.latitude}&slon=${result.position.longitude}&sname=我的位置&dlat=${latitude}&dlon=${longitude}&dname=${address}&dev=0&t=0`;
                            let web_url = `https://uri.amap.com/navigation?from=${result.position.longitude},${result.position.latitude},我的位置&to=${longitude},${latitude},${address}&mode=car&policy=1&src=com.mzwu.www&callnative=0`;
     
                            //尝试唤起高德地图App
                            window.location.href = app_url;
     
                            //唤起失败打开Web高德地图
                            var startTime = Date.now();                            
                            var count = 0;
     
                            var t = setInterval( () => {
                                if (++count < 30) {
                                    return;
                                }
                                if (Date.now() - startTime > 800) {
                                    clearInterval(t);
                                }
                                if (!(document.hidden || document.webkitHidden)) {
                                    window.location.href = web_url;
                                }
                            }, 20);
     
                            window.onblur = function () {
                                clearInterval(t);
                            };
                        }else{
                            alert("获取不到定位，请检查手机设置！");
                        }
                    })
                // })
            })
        },
        getScheme(){
            let agent = navigator.userAgent.toLowerCase();
            if(agent.indexOf("iphone") != -1 || agent.indexOf("ipad") != -1){
                return "iosamap://path";
            }else{
                return "amapuri://route/plan";
            }
        }
            
    }
}

</script>

