<template>
<!--在页面msite_nav导航部分使用swiper-->
<div class="swiper-container" ref="mySwiper">
  <!-- Additional required wrapper -->
  <div class="swiper-wrapper">
    <div class="swiper-slide" v-for="(item, index) in slideData" :key="index" @click="slideClick(item)">
      <!-- <el-image :src="require(`../assets/slides/${item.pic}`)" :fit="'cover'" alt="" /> -->
      <img :src="require(`../assets/slides/${item.pic}`)" :fit="'cover'" alt="" />
      <div class="content"> {{item.title}}</div>
    </div>
  </div>
  <!-- 指示点 -->
  <div class="swiper-pagination"></div>

  <!-- 左右按钮 -->
  <!-- <div class="swiper-button-prev"></div>
  <div class="swiper-button-next"></div> -->

  <!-- 底部滚动条 -->
  <!-- <div class="swiper-scrollbar"></div> -->
</div>
</template>

<script>
import Swiper from 'swiper';
import 'swiper/css/swiper.css';
// import { Navigation, Pagination } from 'swiper/modules';
// import 'swiper/css';
// import 'swiper/css/navigation';
// import 'swiper/css/pagination';

export default {
  name: 'App',
  props: {
    slideData: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      activeIndex: 0
    }
  },
  components: {
 
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  watch: {
    slideData: {
      handler(val, oldVal) {
        console.log('val----', val);
        console.log('oldVal----', oldVal);
        this.$nextTick(() => {
          new Swiper(document.querySelector('.swiper-container'), {
            // 方向：vertical或horizontal
            direction: 'horizontal',
            // 循环
            loop: true,
            // 切换速度
            speed: 600,
            // 自动播放
            autoplay: {
              delay: 2000,
              disableOnInteraction: false
            },
            // 指示点
            pagination: {
              el: '.swiper-pagination',
              clickable: true, // 分页器点可以点击切换
              type: 'bullets', //'bullets' | 'progressbar' | 'fraction' 小圆点|进度条|数字页码
              // dynamicBullets: true,  //动态小圆点(type:'bullets'时)
              // renderBullet(index, className) {
              //   return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
              // },
            },
            // 左右箭头
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            },
            // 底部滚动条
            // scrollbar: {
            //   el: '.swiper-scrollbar',
            // },
          });
        })
      }
    }
  },
  mounted() {
    //创建一个swiper实例来实现轮播
    
  },
  methods: {
    slideClick(item) {
      window.open(item.url)
    }
  }
}
</script>

